/* oswald-regular - cyrillic */
@font-face {
    font-family: 'Oswald';
    font-style: normal;
    font-weight: 400;
    src: url('../fonts/oswald-v26-cyrillic-regular.eot'); /* IE9 Compat Modes */
    src: local(''),
         url('../fonts/oswald-v26-cyrillic-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('../fonts/oswald-v26-cyrillic-regular.woff2') format('woff2'), /* Super Modern Browsers */
         url('../fonts/oswald-v26-cyrillic-regular.woff') format('woff'), /* Modern Browsers */
         url('../fonts/oswald-v26-cyrillic-regular.ttf') format('truetype'), /* Safari, Android, iOS */
         url('../fonts/oswald-v26-cyrillic-regular.svg#Oswald') format('svg'); /* Legacy iOS */
  }
  /* oswald-700 - cyrillic */
  @font-face {
    font-family: 'Oswald';
    font-style: normal;
    font-weight: 700;
    src: url('../fonts/oswald-v26-cyrillic-700.eot'); /* IE9 Compat Modes */
    src: local(''),
         url('../fonts/oswald-v26-cyrillic-700.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('../fonts/oswald-v26-cyrillic-700.woff2') format('woff2'), /* Super Modern Browsers */
         url('../fonts/oswald-v26-cyrillic-700.woff') format('woff'), /* Modern Browsers */
         url('../fonts/oswald-v26-cyrillic-700.ttf') format('truetype'), /* Safari, Android, iOS */
         url('../fonts/oswald-v26-cyrillic-700.svg#Oswald') format('svg'); /* Legacy iOS */
  }

/* rubik-mono-one-regular - cyrillic */
@font-face {
    font-family: 'Rubik Mono One';
    font-style: normal;
    font-weight: 400;
    src: url('../fonts/rubik-mono-one-v8-cyrillic-regular.eot'); /* IE9 Compat Modes */
