.replies {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: $widthSideBlocks;
    border: 2px solid black;
    padding: 5px;
    margin: 5px;
}

.replies h2 {
    font-weight: 500;
}

.avatar img {
    width: 80px;
    border-radius: 50%;
    margin: 5px;
}

.replies_item {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
}

.replies_info {
    display: flex;
    flex-direction: column;
}

.replies_name a{
    font-weight: 700;
    text-decoration: none;
    color: #000;
    
}

.replies_name {
    margin-bottom: 10px;
    font-weight: 700;
}

.replies_text {
    font-size: 12px;
}

.replies .replies_item:nth-child(even) {
    background: gray;
}

.comment_div {
    display: flex;
    align-items: center;
}

#status {
    font-size: 14px;
    margin-left: 5px;
}