header {
    display: flex;
}

.logo {
    width: $widthSideBlocks;
}

.logo img {
    width: 100%;
}

.center_header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

nav {
    margin-left: auto;
    margin-right: auto;
}

.main_menu {
    display: flex;
    list-style: none;
    padding: 0 20px;
}

.sticky-menu {
    position: fixed;
    top: 0;
    left: $widthSideBlocks;
    background: grey;
}

.main_menu li {
    margin-right: 50px;
}
.main_menu li:last-child {
    margin: 0;
}

.main_menu a {
    font-size: $sizeFontMainMenu;
    text-decoration: none;
    font-family: 'Oswald';
    font-weight: $fontWeightMenu;
    color: $colorTextMainMenu;
    padding: $paddingMainMenu;
    display: block;
    border-radius: $borderRadiusMainMenu;
    transition: background 500ms;

    &:hover {
        background: $bgMainMenu;
    }
}

h1 span {
    font-family: 'Rubik Mono One', sans-serif;
    font-size: 70px;
    font-weight: 200;
}

.contacts {
    width: $widthSideBlocks;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.messenger_item {
    display: flex;
    justify-content: flex;
    align-items: center;
}

.logo_messenger {
    margin-right: 5%;
}

.logo_messenger img {
    width: $sizeLogoMessengers;
}

