.banner {
    display: flex;
}

.banner_item {
    width: 23%;
    margin: 1% 1.5% 1.5% 1.5%;
    background: $bgBannerItem;
    height: 270px;
   
}

.title_banner_item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    font-size: $sizeFontTitleBannerItem;
    color: #fff;
    background: $bgMainMenu;
    text-align: center;
    height: $heightTitleBannerItem;
}

.banner_text_item {
    padding: 5px;
}